.emailList {
    flex: 1;
    overflow: scroll;
}

.emailList::-webkit-scrollbar {
    display: none;
    /* scroll-behavior: smooth; */
}

.emailList_settings {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid whitesmoke;
    background-color: white;
    z-index: 999;
    padding-right: 10px;
}

.emailList_sections {
    /* position: sticky;
    top: 0; */
    display: flex;
    background-color: white;
    border-bottom: 1px solid whitesmoke;
    z-index: 999;
}

.emailList_list {
    padding-bottom: 10%;
}